<template>
  <div class="Container-TaskInfomation">
    <div class="calendar">
      <FullCalendar
        ref="fullCalendar"
        class="demo-app-calendar"
        :options="calendarOptions"
        @changeMonth="changeMonth"
      ></FullCalendar>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import FullCalendar from "@fullcalendar/vue";
import dayGridPlugin from "@fullcalendar/daygrid";
import interactionPlugin from "@fullcalendar/interaction";
import timeGridPlugin from "@fullcalendar/timegrid";
import listPlugin from "@fullcalendar/list";

export default {
  name: "DeviceInformation",
  components: {
    FullCalendar,
  },
  mounted() {
    // ref
    // this.$refsfullCalendar
    this.calendarApi = this.$refs.fullCalendar.getApi();
  },
  created() {
    let that = this;
    // console.log(new Date().getMonth() + 1);
    that.thismonth = new Date().getMonth() + 1;
    axios.get("http://47.102.214.37:8080/user/me").then((res) => {
      console.log(res.data);
    });
    let url =
      "http://47.102.214.37:8080/ops/dates/2021/" + (new Date().getMonth() + 1);
    axios.get(url).then((res) => {
      console.log(res);
      for (let i = 0; i < res.data.length; i++) {
        that.calendarOptions.events.push({
          id: res.data[i].schedule.id,
          title: res.data[i].schedule.name,
          start: res.data[i].date,
          backgroundColor: "#409eff",
          borderColor: "#409eff",
          textColor: "#fff",
        });
      }
    });
  },
  data() {
    return {
      thismonth: null, // 

      calendarApi: null,
      calendarOptions: {
        plugins: [
          dayGridPlugin,
          timeGridPlugin,
          interactionPlugin, // needed for dateClick
          listPlugin,
        ],
        headerToolbar: {
          left: "prevYear prev,next nextYear today",
          center: "title",
          right: "dayGridMonth,timeGridWeek,timeGridDay,listMonth",
        },
        buttonText: {
          // headerToolbar / footerToolbarHTML
          today: "",
          month: "",
          week: "",
          day: "",
        },
        events: [],
        initialView: "dayGridMonth",
        editable: true,
        weekends: true,
        dayMaxEvents: true,
        navLinks: true,
        eventClick: this.handleEventClick,
        locale: "zh-cn",
        height: "100%",
        customButtons: {
          // headerToolbar / footerToolbar
          prev: {
            // 
            text: "PREV",
            click: () => {
              this.calendarApi.prev();
              let that = this;
              that.thismonth--;
              let url =
                "http://47.102.214.37:8080/ops/dates/2021/" + that.thismonth;
              axios.get(url).then((res) => {
                console.log(res);
                for (let i = 0; i < res.data.length; i++) {
                  that.calendarOptions.events.push({
                    id: res.data[i].schedule.id,
                    title: res.data[i].schedule.name,
                    start: res.data[i].date,
                    backgroundColor: "#409eff",
                    borderColor: "#409eff",
                    textColor: "#fff",
                  });
                }
              });
            },
          },
          next: {
            // 
            text: "NEXT",
            click: () => {
              this.calendarApi.next();
              let that = this;
              that.thismonth++;
              let url =
                "http://47.102.214.37:8080/ops/dates/2021/" + that.thismonth;
              axios.get(url).then((res) => {
                console.log(res);
                for (let i = 0; i < res.data.length; i++) {
                  that.calendarOptions.events.push({
                    id: res.data[i].schedule.id,
                    title: res.data[i].schedule.name,
                    start: res.data[i].date,
                    backgroundColor: "#409eff",
                    borderColor: "#409eff",
                    textColor: "#fff",
                  });
                }
              });
            },
          },
          today: {
            text: "",
            click: () => {
              this.calendarApi.today();
              let that = this;
              that.thismonth = new Date().getMonth() + 1;
              let url =
                "http://47.102.214.37:8080/ops/dates/2021/" + that.thismonth;
              axios.get(url).then((res) => {
                console.log(res);
                for (let i = 0; i < res.data.length; i++) {
                  that.calendarOptions.events.push({
                    id: res.data[i].schedule.id,
                    title: res.data[i].schedule.name,
                    start: res.data[i].date,
                    backgroundColor: "#409eff",
                    borderColor: "#409eff",
                    textColor: "#fff",
                  });
                }
              });
            },
          },
        },
      },
      currentEvents: [],

      userRole: "", //
      userID: "",
    };
  },
  methods: {
    handleEventClick(clickInfo) {
      console.log(clickInfo);
      let obj = {
        id: clickInfo.event.id,
      };
      this.$router.push({
        path: "./taskDetailInfo",
        query: obj,
      });
    },
    // 
    changeMonth(start, end, current) {
      console.log(
        "changeMonth",
        start.format(),
        end.format(),
        current.format()
      );
    },
  },
};
</script>
